.App {
  text-align: center;
}

.App-logo {
  height: 40vmin;
  pointer-events: none;
}

.swiper-img {
  max-width: 350px;
}

.swiper-container {
  max-width: 350px;
  justify-content: center;
  display: initial;
}

.swiper-img.border-card {
  border-radius: 25px;
}

.swiper-text {
  max-width: 325px;
  justify-content: center;
  display: table-cell;
  padding-top: 2rem;
  font-weight: bold;
  font-size: 1rem;
}

.swiper-pagination {
  z-index: 9999999999 !important;
}

@media (prefers-reduced-motion: no-preference) {
  .App-logo {
    animation: App-logo-spin infinite 20s linear;
  }
}

@media screen and (max-width: 375px) {
  /*.roll-title { padding-right: 0 !important; }*/
}

.psychoeducation-popup>.inner {
  max-width: 425px;
  max-height: 500px;
  margin-top: 2rem;
}

@media screen and (max-width: 375px) {
  .psychoeducation-popup>.inner {
    margin-top: 1.25rem !important;
  }
}

.img-prevalence {
  width: 70% !important;
}

.hover-tip > p {
  background: #e7e7e7;
  border-radius: 5px;
}

.psychoeducation-popup {
  width: 100vw;
  height: 100%;
  /* position: fixed; */
  z-index: 99999;
  position: fixed;
  top: 0;
  left: 0;
  background: white;
  margin: auto;
  margin-left: auto;
  margin-top: auto;
  justify-content: center;
  display: flex;
  -ms-content-zooming: none;
  touch-action: none;
  content-zooming: none;
  overflow-y: hidden;
  overflow-x: hidden;
  overflow-y: none;
  overflow-x: none;
  overflow-x: scroll;
  overflow-y: hidden;
}

img.swiper-img.img-five.border-card {
  height: 33%;
  /* width: 33%; */
}

.note-text.disabled {
  color: lightgray !important;
}

.free-tool-tip {
  z-index: 9999999999999999999999999;
  font-weight: bold;
  padding-top: 1rem;
  padding-bottom: 1rem;
  padding: 1rem !important;
  box-shadow: 2px 2px 0px 0px #000 !important;
}

.pick-title.disabled {
  color: #c8c8c8;
}

.App-header {
  background-color: #fcffdb;
  min-height: 100vh;
  display: flex;
  flex-direction: column;
  align-items: center;
  justify-content: center;
  font-size: calc(10px + 2vmin);
  color: white;
}

.App-link {
  color: #61dafb;
}

@keyframes App-logo-spin {
  from {
    transform: rotate(0deg);
  }

  to {
    transform: rotate(360deg);
  }
}

.App-header {
  max-width: 425px;
  margin-left: auto;
  margin-right: auto;
  background: #f7ffdd;
  color: darkslategray;
}

.sticky-top {
  background: #f7ffdd;
  border-bottom: 1px solid black;
  margin-bottom: 1rem !important;
  padding-top: 1rem !important;
}

.question-box {
  font-size: .8rem;
  text-align: justify;
}

.question-item-box {
  margin-left: auto;
  margin-right: auto;
  margin-top: auto;
  margin-bottom: auto;
  font-size: .5rem;
}

.question-answer-select {
  height: 28px;
  width: 28px;
  background-image: none;
  padding: 0 !important;
  border-radius: 25px !important;
  text-indent: 9px;
}

ul.choices {
  display: flex;
  list-style-type: none;
  padding: 0;
}

ul.choices>li {
  width: 33%;
  background: white;
  border: 1px solid grey;
  border-radius: 15px;
}

/* Checkbox Form */

.sound-form {
  padding: .8rem;
}

.pick-title {
  font-size: 1.5rem;
  text-align: left;
  font-weight: 800;
  margin-left: 0.25rem;
}

.DigitRoll__Slice {
  width: 30px;
}


input.sound-check-box {
  position: absolute;
  top: 0;
  left: 0;
  width: 100%;
  height: 100%;
  border-radius: 10px;
  border: 1px solid grey;
}

input.sound-check-box:checked {
  border: 2px solid #16f65a;
  background: none;
}

input.sound-check-box:disabled {
  background: #cecece7d;
}

.component-title-container {
  line-height: 1.2rem;
}

.component-title-container>.img-container {
  margin-bottom: 1rem;
}

body {
  background: #242424;
}

.sound-container>input[type="checkbox"] {
  /* Add if not using autoprefixer */
  -webkit-appearance: none;
  appearance: none;
  /* For iOS < 15 to remove gradient background */

  /* Not removed via appearance */
  margin: 0;
}

